import React from 'react';
import { graphql } from 'gatsby';
import PropTypes from 'prop-types';
import Helmet from 'react-helmet';
import styled from '@emotion/styled';
import {
	Header,
	CardListItem,
	CardListWrapper,
	ArticleListItem,
} from '../components';
import { Layout } from '../layouts';

export const ContentWrapper = styled.main`
	display: flex;
	flex-wrap: wrap;
	margin: 4rem auto 1rem;
	max-width: ${props => props.theme.layout.base};
	@media (max-width: ${props => props.theme.breakpoints.l}) {
		margin: 3rem 1rem 1rem;
	}
`;

const ReleaseNotes = styled.section`
	width: 65%;
	padding-right: 2rem;
	@media (max-width: 1100px) {
		width: 100%;
		padding-right: 0;
	}
`;
const ArticleSection = styled.aside`
	width: 35%;
	padding: 1rem;
	background: ${props => props.theme.colors.white.grey};
	@media (max-width: 1100px) {
		width: 100%;
	}
`;
const ArticleList = styled.ul`
	list-style: none;
	margin: 0;
`;

const Index = ({ coverImage, articles, pageDescription, latestReleases }) => {
	return (
		<Layout>
			<Helmet title={'Expedia Group Open Source Updates'} />
			<Header title="Expedia Group Open Source Updates" cover={coverImage} />
			<ContentWrapper>
				<p dangerouslySetInnerHTML={{ __html: pageDescription }} />
				<ReleaseNotes>
					<CardListWrapper>
						{latestReleases.map(release => (
							<CardListItem
								key={release.id}
								cover={release.imageUrl}
								path={release.url}
								title={release.repoName}
								excerpt={release.name}
								date={release.stringDate}
								isExternal={true}
								customColConfig={{ xl: 2, lg: 2 }}
							/>
						))}
					</CardListWrapper>
				</ReleaseNotes>
				<ArticleSection>
					<h3>Latest From the Blog</h3>
					<ArticleList>
						{articles.map(article => (
							<ArticleListItem key={article.id} article={article} />
						))}
					</ArticleList>
				</ArticleSection>
			</ContentWrapper>
		</Layout>
	);
};

const mapReposToReleases = repos =>
	repos.reduce((prev, nextRepo) => {
		const { releases, ...restOfRepo } = nextRepo;
		if (releases.nodes.length) {
			const releasesWithRepo = releases.nodes.map(release => ({
				...restOfRepo,
				...release,
			}));
			return prev.concat(...releasesWithRepo);
		}
		return prev;
	}, []);

function mapDataToProps({ data }) {
	const coverImage = data.coverImage.childImageSharp.fluid;
	const repos = data.releaseNotes.data.releaseNoteRepos;
	const articles = data.articles.nodes.slice(0, data.site.siteMetadata.articleLimit)
	const { html } = data.pageDescription;
	const latestReleases = mapReposToReleases(repos).sort(
		(a, b) => new Date(b.isoDate) - new Date(a.isoDate)
	);

	return {
		coverImage,
		articles,
		pageDescription: html,
		latestReleases,
	};
}

export default props => <Index {...mapDataToProps(props)} />;

Index.propTypes = {
	data: PropTypes.shape({
		releaseNotes: PropTypes.shape({
			data: PropTypes.shape({
				releaseNoteRepos: PropTypes.arrayOf(
					PropTypes.shape({
						releases: PropTypes.shape({
							nodes: PropTypes.shape({
								url: PropTypes.string,
								stringDate: PropTypes.string,
								isoDate: PropTypes.string,
								id: PropTypes.string,
								name: PropTypes.string,
							}),
						}),
						repoName: PropTypes.string,
						id: PropTypes.string,
						imageUrl: PropTypes.object,
					})
				),
			}),
		}),
		posts: PropTypes.shape({
			nodes: PropTypes.arrayOf(
				PropTypes.shape({
					imageUrl: PropTypes.shape({
						childImageSharp: PropTypes.object,
						extension: PropTypes.string,
						publicURL: PropTypes.string,
					}),
					mediumUrl: PropTypes.string,
					publishedDate: PropTypes.string,
					subtitle: PropTypes.string,
					title: PropTypes.string,
					author: PropTypes.string,
					id: PropTypes.string,
				}).isRequired
			),
		}),
		coverImage: PropTypes.shape({
			childImageSharp: PropTypes.object,
		}),
		pageDescription: PropTypes.shape({
			html: PropTypes.string,
		}),
		site: PropTypes.shape({
			siteMetadata: PropTypes.shape({
				title: PropTypes.string,
			}).isRequired,
		}),
	}),
};

export const query = graphql`
	query {
		releaseNotes: githubData {
			data {
				releaseNoteRepos {
					releases {
						nodes {
							url
							stringDate: publishedAt(fromNow: true)
							isoDate: publishedAt
							id
							name
						}
					}
					repoName: name
					id
					imageUrl {
						...CardListImageFluid
						extension
						publicURL
					}
				}
			}
		}
		articles: allMediumPosts(sort: { order: DESC, fields: [publishedDate] }) {
			nodes {
				imageUrl {
					...CardListImageFluid
					extension
					publicURL
				}
				mediumUrl
				publishedDate(fromNow: true)
				title
				author
				id
			}
		}
		pageDescription: markdownRemark(
			frontmatter: { type: { eq: "pageDescription" }, page: { eq: "index" } }
		) {
			html
		}
		coverImage: file(relativePath: { eq: "field-header.jpg" }) {
			...HeaderImageFluid
		}
		site {
			siteMetadata {
				articleLimit
			}
		}
	}
`;
